body,
html {
	height: 100%;
	margin: 0;
	color: #444444;
	font-family: Lato, sans-serif;
}
.menu {
	position: fixed;
	display: flex;
	flex-direction: column;
	align-items: center;
	right: 0;
	top: 50%;
	overflow: hidden;
	transform: translate(-10%, -50%);
	border-radius: 10px;
	background-color: rgba(0, 0, 0, 0.15);
}
.icons {
	display: flex;
	flex: 1;
	flex-direction: column;
	justify-content: space-between;
}
.menu .btnmenu {
	display: flex;
	flex-direction: column;
	width: 7vh;
	height: 7vh;
	justify-content: center;
}
.menuhover:hover {
	background-color: rgba(0, 0, 0, 0.6);
}
.icon {
	fill: #f5f5f5;
	padding: 5px;
}
.icon:hover {
	fill: #4cff4c;
}
.profile {
	display: flex;
	/* flex-direction: row; */
	align-items: center;
	justify-content: center;
	height: 100vh;
	/* width:60%; */
	background-color: #f5f5f5;
	padding-left: 50px;
	padding-right: 50px;
	flex-flow: wrap;
	/* flex-wrap: nowrap; */
}
.profile .desc {
	/* background-color: #4cff4c; */
			/* flex-grow:1; */
	width: 60vh;
	flex-direction: column;
}
.profile .img {
	/* background-color:#ef596f; */
	/* flex-grow:1; */
	width: 60vh;
	height: 60vh;
	background-size: contain;
	background-repeat: no-repeat;
	background-position: center;
}
.profile h1 {
	/* font-family: Lato, sans-serif; */
	margin: 0;
}
.profile i {
	font-family: monospace;
}
.jumbotron {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100vh;
	/* background-color: #c0c0c0; */
	background: linear-gradient(180deg, #f5f5f5 0%, #aaaaaa 100%);
}
.skills {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	min-height: 100vh;
	background-color: #f5f5f5;
}
.skills h1 {
	/* font-family: Lato, sans-serif; */
	margin-bottom: 5vw;
}
.skills .list {
	flex-wrap: nowrap;
	width: 50%;
	display: flex;
	flex-flow: wrap;
	text-align: center;
	margin-bottom: 5vw;
}
.skills .item {
	overflow: auto;
	margin: 10px;
	padding: 20px;
	background-color: rgb(255, 255, 255);
	flex-grow: 1;
	border-radius: 10px;
	box-shadow: rgb(170, 170, 170) 0 6px 8px;
	overflow-wrap: break-word;
}
.skills .item p {
	font-size: 18px;
	font-family: monospace;
}
.desc .myname {
	font-weight: 900;
	font-size: 5vh;
	width: 100%;
}
.desc .focus {
	width: 100%;
	font-weight: 900;
	font-size: 3vh;
}
.desc .as {
	width: 100%;
	/* margin-top: 20px; */
	margin-bottom: 20px;
	/* font-weight: 900;
	font-size: 3vh; */
}
.desc p {
	/* font-family: Lato, sans-serif; */
	margin: 0;
}
.footer {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 25vh;
	font-family: monospace;
	font-size: 1.8vh;
	color: #f5f5f5;
	background-color: #111111;
}
.footercontent {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	justify-content: center;
}
.footericon {
	margin: 10px;
}
.code {
	position: absolute;
	width: 54vw;
	height: 43vw;
	background: #1D1E22;
	box-shadow: 0 4px 30px rgba(0, 0, 0, 0.5);
	border-radius: 6px;
	z-index: 5;
}
.code header {
	grid-template-columns: 36px auto 36px;
	height: 4vw;
	display: flex;
	align-items: center;
	justify-content: center;
}
.code header svg {
	margin: 10px;
}
.code header h1 {
	width: 58vw;
	/* font-family: Lato, sans-serif; */
	font-weight: 900;
	font-size: 1.4vw;
	letter-spacing: 1.57px;
	color: #C5C8D4;
	margin: 0;
}
.code header .header {
	width: 1.4vw;
}
.code .code-content {
	margin: 0 10px 10px;
	color: white;
	font-size: 1.4vw;
}
.code .code-content .c-1 {
	color: #ca5fde;
}
.code .code-content .c-2 {
	color: #e5c07b;
}
.code .code-content .c-3 {
	color: #74B087;
}
.code .code-content .c-4 {
	color: #DE7300;
}
.code .code-content .c-5 {
	color: #61afef;
}
.code .code-content .c-6 {
	color: #ef596f;
}
.code-jsx .code-content code .desc-1,
.code-jsx .code-content code .desc-2,
.code-jsx .code-content code .desc-3,
.code-jsx .code-content code .desc-4,
.code-jsx .code-content code .desc-5,
.code-jsx .code-content code .desc-6,
.code-jsx .code-content code .desc-7,
.code-jsx .code-content code .desc-8,
.code-jsx .code-content code .line-1,
.code-jsx .code-content code .line-10,
.code-jsx .code-content code .line-11,
.code-jsx .code-content code .line-2,
.code-jsx .code-content code .line-3,
.code-jsx .code-content code .line-4,
.code-jsx .code-content code .line-5,
.code-jsx .code-content code .line-6,
.code-jsx .code-content code .line-8,
.code-jsx .code-content code .line-9 {
	width: 0;
	margin: 0;
	border-right: 0.15em solid transparent;
	overflow: hidden;
	display: inline-block;
	white-space: nowrap;
}
.code-jsx .code-content code .line-1 {
	animation: typing-1 2s 0s steps(37, end) forwards, cursor 1s linear 0s 3;
}
.code-jsx .code-content code .line-2 {
	animation: typing-2 1s 3s steps(13, end) forwards, cursor 1s linear 3s 2;
}
.code-jsx .code-content code .line-3 {
	animation: typing-3 2.5s 5s steps(56, end) forwards, cursor 1s linear 5s 3.5;
}
.code-jsx .code-content code .line-4 {
	animation: typing-4 1s 8.5s steps(10, end) forwards, cursor 1s linear 8.5s 1.5;
}
.code-jsx .code-content code .line-5 {
	animation: typing-5 0.5s 10s steps(8, end) forwards, cursor 1s linear 10s 1;
}
.code-jsx .code-content code .line-6 {
	animation: typing-6 0.5s 11s steps(6, end) forwards, cursor 1s linear 11s 1;
}
.code-jsx .code-content code .desc-1 {
	animation: typing-desc-1 2.5s 12s steps(51, end) forwards, cursor 1s linear 12s 2.5;
}
.code-jsx .code-content code .desc-2 {
	animation: typing-desc-2 2.5s 14.5s steps(49, end) forwards, cursor 1s linear 14.5s 2.5;
}
.code-jsx .code-content code .desc-3 {
	animation: typing-desc-3 2.5s 17s steps(47, end) forwards, cursor 1s linear 17s 2.5;
}
.code-jsx .code-content code .desc-4 {
	animation: typing-desc-4 2.5s 19.5s steps(43, end) forwards, cursor 1s linear 19.5s 2.5;
}
.code-jsx .code-content code .desc-5 {
	animation: typing-desc-5 2.5s 22s steps(45, end) forwards, cursor 1s linear 22s 2.5;
}
.code-jsx .code-content code .desc-6 {
	animation: typing-desc-6 2s 24.5s steps(41, end) forwards, cursor 1s linear 24.5s 2;
}
.code-jsx .code-content code .desc-7 {
	animation: typing-desc-7 2s 26.5s steps(35, end) forwards, cursor 1s linear 26.5s 2;
}
.code-jsx .code-content code .desc-8 {
	animation: typing-desc-8 2s 28.5s steps(28, end) forwards, cursor 1s linear 28.5s 2;
}
.code-jsx .code-content code .line-8 {
	animation: typing-8 1s 30.5s steps(7, end) forwards, cursor 1s linear 30.5s 1;
}
.code-jsx .code-content code .line-9 {
	animation: typing-9 0.5s 31.5s steps(2, end) forwards, cursor 1s linear 31.5s 0.5;
}
.code-jsx .code-content code .line-10 {
	animation: typing-10 0.5s 32s steps(2, end) forwards, cursor 1s linear 32s 0.5;
}
.code-jsx .code-content code .line-11 {
	border-color: transparent;
	animation: typing-11 0.5s 32.5s steps(2, end) forwards, cursor-persist 1s linear 32.5s infinite;
}
.code-jsx .code-content code .line-10,
.code-jsx .code-content code .line-4 {
	margin-left: 2vw;
}
.code-jsx .code-content code .line-5,
.code-jsx .code-content code .line-9 {
	margin-left: 4vw;
}
.code-jsx .code-content code .line-6,
.code-jsx .code-content code .line-8 {
	margin-left: 6vw;
}
.code-jsx .code-content code .desc-1,
.code-jsx .code-content code .desc-2,
.code-jsx .code-content code .desc-3,
.code-jsx .code-content code .desc-4,
.code-jsx .code-content code .desc-5,
.code-jsx .code-content code .desc-6,
.code-jsx .code-content code .desc-7,
.code-jsx .code-content code .desc-8 {
	margin-left: 8vw;
}
