@keyframes cursor {
	20%,
	80%,
	from,
	to {
		border-color: transparent;
	}
	25%,
	75% {
		border-color: white;
	}
}
@keyframes cursor-persist {
	45%,
	from {
		border-color: transparent;
	}
	50%,
	to {
		border-color: white;
	}
}